import React, { Component } from 'react';
import { bindActionCreators } from 'redux';
import { connect } from 'react-redux';
import { fetchStarwars, fetchSchema } from '../actions/starwars';
import Item from '../components/Item';
import Filter from '../components/Filter';
import ItemList from '../components/ItemList';

class ItemListContainer extends Component {

  constructor(props, context) {
    super(props, context)
    this.state = {
      filters: [],
      values: []
    }
  }

  filtrerLesItems(filter, value)
  {
    // console.log(filter);
    // console.log(value);
    let itemToKeep = [];
    this.props.items.item.results.forEach(function(item){
      for (var variable in item) {
        if (item.hasOwnProperty(variable)) {
          if (variable === filter && item[variable].includes(value)) {
            itemToKeep.push(item);
          }
        }
      }
    })
    this.props.items.item.results = itemToKeep;

  }

  handleChange = (e) =>
  {
    let filter = e.target.placeholder;
    let value = e.target.value;


    this.state.filters.push(filter);
    this.state.values.push(value);
  }



  renderList() {
    // must wrap .map block with conditional statement
    if (this.props.items.item.results) {
      this.props.fetchSchema(this.props.items.section)

      // console.log('on affiche le this state');
      // console.log(this.state);
      this.state.filters.map((filterToApply, id) => (
        this.filtrerLesItems(filterToApply, this.state.values[id])
      ))

      let filtersAvailable = "";
      if (this.props.items.schema) {
        filtersAvailable = this.props.items.schema.required.map((filter, id) => (
          <Filter key={id} filter={filter} onchange={this.handleChange} />
        ));
      }else {
        filtersAvailable = <a onClick={() => this.props.fetchSchema(this.props.items.section)}>Recherche en fonction</a>
      }

      const children = this.props.items.item.results.map((item) => (
        <Item key={item.name} item={item} />
      ));
      let prevPage = "";
      if(this.props.items.item.previous)
        prevPage = <a onClick={() => this.props.fetchStarwars(this.props.items.item.previous)}>Page précèdente</a>;
      let nextPage = "";
      if(this.props.items.item.next)
        nextPage = <a onClick={() => this.props.fetchStarwars(this.props.items.item.next)}>Page suivante</a>;
      return (
        <div>
        <h1>{this.props.items.section}</h1>
        {filtersAvailable}
        <ItemList>
          {children}
        </ItemList>
        {prevPage}
        {nextPage}
        </div>
      );
    }
  }

  render() {
    return (
      <div>
        {this.renderList()}
      </div>
    )
  }
}

function mapStateToProps(state) {
  return {
    items: state.items
  };
}

function matchDispatchToProps(dispatch){
    return bindActionCreators({fetchStarwars: fetchStarwars, fetchSchema: fetchSchema}, dispatch);
}

export default connect(mapStateToProps, matchDispatchToProps)(ItemListContainer);



