const apiURL = "https://swapi.co/api/";

export function fetchStarwars(section, filters, values) {
  let url = "";

  if (section.includes('page'))
  {
    url = section;
    let tabUrl = section.split('/');
    section = tabUrl[tabUrl.length-2];
  }
  else
  {
    url = apiURL + section;
  }
  if (section.includes('/'))
    section = section.slice(0,-1);

  section = section.charAt(0).toUpperCase() + section.slice(1);

  return (dispatch) => {
    fetch(url)
    .then((results) => {return results.json()})
    .then(
      data => dispatch({
        type: 'FETCH_STARWARS',
        payload: data,
        section: section,
        filters: filters,
        values: values
      })
    )
    .catch(error => {
      throw(error);
    });
  }
}

export function fetchSchema(section)
{
  console.log('on cherche le schema : '+section);
  return (dispatch) => {
    fetch(apiURL + section.toLowerCase() + '/schema')
    .then((results) => {return results.json()})
    .then(data => dispatch({
      type: 'FETCH_SCHEMA',
      schema : data
    }))
    .catch(error => {
      throw(error);
    });
  }
}

