import React, { Component } from 'react';
import { DropdownButton, MenuItem } from 'react-bootstrap';
// import { fetchImages } from '../actions/imgur';
import { fetchStarwars, fetchSchema } from '../actions/starwars';
import { connect } from 'react-redux';
import { bindActionCreators } from 'redux';

class Search extends Component {
  handleSelect = (eventKey) => {
    this.props.items.schema = null;
    this.props.fetchStarwars(eventKey);
    this.props.fetchSchema(eventKey);
  }

  render() {
    return(
      <DropdownButton title={this.props.items.section} id="bg-nested-dropdown" onSelect={ this.handleSelect }>

        <MenuItem eventKey="films">Films</MenuItem>
        <MenuItem eventKey="people">Personnages</MenuItem>
        <MenuItem eventKey="planets">Planètes</MenuItem>
        <MenuItem eventKey="species">Espèces</MenuItem>
        <MenuItem eventKey="starships">Vaisseaux</MenuItem>
        <MenuItem eventKey="vehicles">Vehicules</MenuItem>

      </DropdownButton>
    )
  }
}

function mapStateToProps(state) {
  return {
    items: state.items,
  };
}

function matchDispatchToProps(dispatch){
    return bindActionCreators({fetchStarwars: fetchStarwars, fetchSchema: fetchSchema}, dispatch);
}

export default connect(mapStateToProps, matchDispatchToProps)(Search);



