import React from 'react';
import { Col } from 'react-bootstrap';

const Filter = (props) => {
    return (
      <Col md={2}>
        <div class="filter" id={props.id}>
          <input type="text" placeholder={props.filter}

            onChange={props.onchange}
          />
        </div>
      </Col>
    )
}
export default Filter;



