import React from 'react';
import { Col, Button } from 'react-bootstrap';

const Item = (props) => {
    return (
      <Col md={2}>
        <div class="item" id={props.id}>
          <p>{props.item.name}</p>
        </div>
      </Col>
    )
}
export default Item;



